.LinkContainer {
  display: inline-block;
  .LinkList {
    padding-left: 0px;
    list-style-type: none;
    transition: all 0.5s;

    &-callToAction {
      @extend .LinkList;
      border-radius: 15px;

      &:hover {
        -webkit-box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
        -moz-box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
        box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
        background-color: rgba(255, 255, 255, 0.8);
      }
    }

    &-download {
      @extend .LinkList;
      -webkit-box-shadow: 0px 14px 35px -6px rgba(114, 114, 114, 0.2);
      -moz-box-shadow: 0px 14px 35px -6px rgba(114, 114, 114, 0.2);
      box-shadow: 0px 14px 35px -6px rgba(114, 114, 114, 0.2);

      .LinkList__ListItem {
        padding: 5% 0px;
      }
    }

    &__Title {
      height: 50px;
      color: white;
      font-size: 20px;
      line-height: 50px;
      text-align: center;

      &-callToAction {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        @extend .LinkList__Title;
        background: $calltoActionGradient;
      }

      &-download {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        @extend .LinkList__Title;
        background-color: #3b434b;
      }
    }

    &__Text {
      font-size: 18px;
    }

    li {
      text-align: center;
      border-bottom: 1px solid rgb(196, 196, 196);

      &:last-child {
        border-bottom: none;
      }
    }

    &__Link {
      color: #808080;

      &:hover {
        svg {
          transition: all 0.5s;
          transform: scale(1.2) translateY(5px);
        }
      }
    }

    .callToActionSvg,
    .downloadSvg {
      width: 35px;
    }

    .callToActionSvg {
      transform: rotate(-90deg);

      #Path_cycle {
        fill: rgba(0, 0, 0, 0);
      }

      #Path_arrow {
        opacity: 1;
        fill: #28b781;
      }
    }
  }
}

.StageContainer {
  display: inline-block;

  .StageList {
    height: 200px;
    width: 150px;
    border-radius: 15px;
    border-left: 1px solid rgb(196, 196, 196);
    border-right: 1px solid rgb(196, 196, 196);
    border-bottom: 1px solid rgb(196, 196, 196);
    transition: all 0.5s;
    text-align: center;

    &:hover {
      -webkit-box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
      -moz-box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
      box-shadow: 0px 14px 35px -6px rgba(79, 221, 209, 0.4);
      background-color: rgba(255, 255, 255, 0.8);
    }

    &__Title {
      height: 50px;
      color: white;
      font-size: 15px;
      line-height: 50px;
      text-align: center;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
      background: $workshopGradientVertical;
    }
  }
}

@media (max-width: 770px) {
  .LinkContainer:nth-child(2n) {
    float: left;
  }

  .LinkContainer:nth-child(2n+1) {
    float: right;
  }
}
